import React, { useEffect, useRef, useState, useCallback } from 'react'
import { useEventListener } from '../../hooks/hooks'
import gsap from 'gsap'

import './Cursor.scss'

function Cursor({ isHover }) {
  const innerRef = useRef()
  const centerRef = useRef()
  const [isVisible, setIsVisible] = useState(true)
  const mouseenterHandler = useCallback(() => setIsVisible(true), [])
  const mouseleaveHandler = useCallback(() => setIsVisible(false), [])
  useEventListener('mouseenter', mouseenterHandler, document)
  useEventListener('mouseleave', mouseleaveHandler, document)

  useEffect(() => {
    const updateCursorPosition = e => {
      mouse.x = e.clientX
      mouse.y = e.clientY
    }
    const mouse = {
      x: 0,
      y: 0
    }
    const updateCursor = () => {
      const cursorInner = innerRef.current
      const cursorCenter = centerRef.current

      gsap.set(cursorCenter, {
        left: mouse.x,
        top: mouse.y
      });

      gsap.to(cursorInner, {
        duration: 0.2,
        x: mouse.x,
        y: mouse.y
      });

      frameId = requestAnimationFrame(updateCursor);
    }
    let frameId = null

    document.body.classList.add('with-custom-cursor')
    document.body.addEventListener('pointermove', updateCursorPosition)

    updateCursor()

    return () => {
      document.body.classList.remove('with-custom-cursor')
      document.body.removeEventListener('pointermove', updateCursorPosition)
      cancelAnimationFrame(frameId)
    }
  }, [])

  useEffect(() => {
    isHover
      ? gsap.to(innerRef.current, { borderRadius: "50%" })
      : gsap.to(centerRef.current, { borderRadius: "0" })
  }, [isHover])

  useEffect(() => {
    const cursorElement = [innerRef.current, centerRef.current]

    isVisible
      ? gsap.set(cursorElement, { opacity: 1 })
      : gsap.set(cursorElement, { opacity: 0 })
  }, [isVisible])
  return (
    <>
      <div className="cursor__inner" ref={innerRef}></div>
      <div className="cursor__center" ref={centerRef}></div>
    </>
  )
}

export default Cursor